.page-arrows {
  padding: 0rem 1.5rem;
  margin-bottom: 1rem;
}

.page-arrows div i {
  background-color: white;
  border-radius: 50%;
  -webkit-box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
          box-shadow: 3px 3px 15px rgba(0,0,0,0.4);
  color: #333;
  font-size: 36px;
  padding: 0.35rem;
  margin: 0;
}


@media screen and (max-width: 768px) {

  .page-arrows, .page-arrows div i {
    display: none;
  }
}



